.svg {
    font: 12.5em 'Monoton';
    width: 100%;
    height: 100vh;
    margin: 150px 0 0 0;
}

.content{
  text-align: center;
}

.text {
    fill: none;
    stroke-dasharray: 8% 29%;
    stroke-width: 5px;
    stroke-dashoffset: 1%;
    animation: stroke-offset 5.5s infinite linear;
}

.text:nth-child(1){
	stroke: #1c234d;
	animation-delay: -1;
}

.text:nth-child(2){
	stroke: #315b2c;
	animation-delay: -2s;
}

.text:nth-child(3){
	stroke: #2f8f7f;
	animation-delay: -3s;
}

.text:nth-child(4){
	stroke: #2f8f7f;
	animation-delay: -4s;
}

.text:nth-child(5){
	stroke: #da2717;
	animation-delay: -5s;
}

@keyframes stroke-offset{
	100% {
    stroke-dashoffset: -35%;
  }
}

.centeredDiv {
    display: grid;
    justify-items: center;
    align-items: center;
    height: 60%;
    width: 100%;
    justify-content: center;
    align-content: center;
}